<template>
    <div>
        <BaseHead level="3">
            История выводов

            <template #help>
                <BaseButton
                    mode="rounded"
                    size="small"
                    type="secondary"

                    class="tooltip__caller-button"
                    @click="modalStatusHelp = true"
                >
                    <font-awesome-icon icon="question"/>
                </BaseButton>
            </template>

            <template #actions>
                <BaseButton
                    size="small"
                    @click="modalWithdraw = true"
                >
                    Создать вывод
                </BaseButton>
            </template>
        </BaseHead>
        <div class="withdraw">
            <template v-for="(withdraw, i) in withdrawHistory">
                <div class="withdraw__paysystem paysystem">
                    <div
                        class="paysystem__image"
                        :style="{ backgroundImage: `url(${getPaySystemData(withdraw.pay_system, withdraw.wallet).image})` }"
                    ></div>
                    <div class="paysystem__info">
                        {{ getPaySystemData(withdraw.pay_system, withdraw.wallet).name }}
                        <div class="paysystem__wallet">
                            <template v-if="withdraw.pay_system == 1">
                                {{ withdraw.wallet | formatPhone }}
                            </template>
                            <template v-else-if="withdraw.pay_system == 4">
                                {{ withdraw.wallet | formatCard }}
                            </template>
                            <template v-else>
                                {{ withdraw.wallet }}
                            </template>
                        </div>
                    </div>
                </div>
                <div class="withdraw__sum">
                    {{ withdraw.amount | formatMoney }}
                </div>
                <div class="withdraw__date">
                    {{ withdraw.date | formatDatetime }}
                </div>
                <div :key="`withdraw-status-${i}`" class="withdraw__status">
                    <BaseStatus
                        class="withdraw__status-label"
                        v-bind="withdraw.status"
                    />
                </div>
                <div class="withdraw__line"></div>
            </template>
        </div>
        <div class="withdraw-action">
            <BaseButton
                label="Загрузить еще"
                type="secondary"
                mode="block"
                :loading="statusLoadingHistory"
                @click="loadHistory"
            />
        </div>

        <BaseModal
            :show.sync="modalStatusHelp"
            title="Статусы вывода"
            :card="true"
        >
            <div class="modal-status">
                <div class="modal-status__status">
                    <BaseStatus v-bind="STATUSES[0]"/>
                </div>
                Вывод создан в платежной системе, но еще не проведен.
                <div class="modal-status__status">
                    <BaseStatus v-bind="STATUSES[1]"/>
                </div>
                Вывод успешно проведен.
                <div class="modal-status__status">
                    <BaseStatus v-bind="STATUSES[2]"/>
                </div>
                Вывод отменен и возвращен на баланс сервиса. Такое может произойти из-за того, что указан несуществующий номер счета в платежной системе.
                Другой вариант — у Вашего аккаунта в платежной системе достигнуты лимиты и платежная система не дает совершить перевод. Попробуйте в данном случае выбрать другой способ вывода.
            </div>
        </BaseModal>

        <BaseModal
            :show.sync="modalWithdraw"
            title="Вывод средств"
        >
            <BaseForm @submit="onWithdraw">
                <BaseFormField
                    type="number"
                    label="Сумма"
                    placeholder="100"
                    :min="10"
                    :max="15000"
                    name="amount"
                    required
                    decimal

                    @update:value="inputAmount = $event"
                >
                    <template #actionEnd>
                        <BaseLink>Весь баланс</BaseLink>
                    </template>
                </BaseFormField>
                <BaseFormField
                    ref="fieldSelectWallet"

                    type="select"
                    label="Способ вывода"
                    placeholder="Выберите способ вывода"
                    name="wallet"
                    required

                    :options="walletsAsOptions"
                    @select="onSelectEditWallets"
                >
                    <template #message>
                        Вы можете создать до {{ maxWalletsCount }} способов вывода. <BaseLink @click="modalEditWallets = true">Редактировать</BaseLink>
                    </template>
                </BaseFormField>
                <BaseFormButton>
                    Вывести
                    <template v-if="inputAmount">
                        {{ inputAmount | formatMoney }}
                    </template>
                </BaseFormButton>
            </BaseForm>
        </BaseModal>

        <BaseModal
            :show.sync="modalEditWallets"
            title="Способы вывода"
        >
            <BaseMessage
                v-if="wallets.length >= maxWalletsCount"
                type="warning"
                mode="block"
            >
                Вы создали максимально возможное количество способов вывода. Удалите неиспользуемые способы вывода, чтобы добавить новые.
            </BaseMessage>

            <div
                v-if="wallets.length > 0"
                class="wallets"
            >
                <ElementItem
                    v-for="wallet in wallets"
                    class="wallet"
                >
                    <div class="paysystem">
                        <div
                            class="paysystem__image"
                            :style="{ backgroundImage: `url(${getPaySystemData(wallet.pay_system, wallet.wallet).image})` }"
                        ></div>
                        <div class="paysystem__info">
                            {{ getPaySystemData(wallet.pay_system, wallet.wallet).name }}
                            <div class="paysystem__wallet">
                                <template v-if="wallet.pay_system == 1">
                                    {{ wallet.wallet | formatPhone }}
                                </template>
                                <template v-else-if="wallet.pay_system == 4">
                                    {{ wallet.wallet | formatCard }}
                                </template>
                                <template v-else>
                                    {{ wallet.wallet }}
                                </template>
                            </div>
                        </div>
                    </div>

                    <template #actions>
                        <BaseLink @click="deleteWallet(wallet.id)">Удалить</BaseLink>
                    </template>
                </ElementItem>
            </div>

            <BaseForm
                v-if="wallets.length < maxWalletsCount"
                @submit="addWallet"
            >
                <BaseFormField
                    type="select"
                    label="Платежная система"
                    placeholder="Выберите платежную систему для вывода"
                    name="pay_system"
                    required
                    :options="paySystemsForm"

                    @select="editWalletsAddSelectedType = $event"
                >
                    <template #message>
                        Созданные способы выводы будут доступны только Вам
                    </template>
                </BaseFormField>
                <template v-if="editWalletsAddSelectedType == 1">
                    <BaseFormField
                        key="wallet-type-qiwi"

                        type="phone"
                        label="Номер кошелька"
                        name="wallet"
                        placeholder="+7 912 345-67-89"
                        required
                    />
                </template>
                <template v-else-if="editWalletsAddSelectedType == 2">
                    <BaseFormField
                        key="wallet-type-yandex"

                        type="mask"
                        label="Номер кошелька"
                        name="wallet"
                        placeholder="4100000000000000"
                        required
                        minlength="11"
                        inputmode="numeric"
                        :mask="{
                            mask: '41##0000000[000000000]',
                            definitions: {
                                '#': /0/,
                                '4': /4/,
                                '1': /1/,
                            }
                        }"
                    />
                </template>
                <template v-else-if="editWalletsAddSelectedType == 3">
                    <BaseFormField
                        key="wallet-type-webmoney"

                        type="mask"
                        label="Номер кошелька WMR"
                        name="wallet"
                        placeholder="R123456789000"
                        required
                        minlength="13"
                        :mask="{
                            mask: '{R}000000000000',
                        }"
                    />
                </template>
                <template v-else-if="editWalletsAddSelectedType == 4">
                    <BaseFormField
                        key="wallet-type-bank-card"

                        type="card"
                        label="Номер карты"
                        name="wallet"
                        required
                    />
                </template>
                <BaseFormButton label="Добавить"/>
            </BaseForm>
        </BaseModal>
    </div>
</template>

<script>
import ElementSkeleton from '../components/ElementSkeleton';
import BankCard from '../js/BankCard';
import ElementItem from '../components/ElementItem';

export default {
    name: 'MainWithdraw',
    components: { ElementItem, ElementSkeleton },

    data() {
        return {
            withdrawHistory: [],

            modalWithdraw: false,
            modalStatusHelp: false,
            modalEditWallets: false,

            inputAmount: null,
            statusLoadingHistory: false,

            editWalletsAddSelectedType: null,

            paySystems: {
                1: {
                    id: 1,
                    name: 'Qiwi',
                    image: require('../assets/paysystems/qiwi.svg'),
                },
                2: {
                    id: 2,
                    name: 'Yandex',
                    image: require('../assets/paysystems/yandex.money.svg'),
                },
                3: {
                    id: 3,
                    name: 'WebMoney',
                    image: require('../assets/paysystems/webmoney.svg'),
                },
                4: {
                    id: 4,
                    name: 'Банковская карта',
                },
            },

            maxWalletsCount: 10,

            wallets: [
                {
                    id: 312312,
                    pay_system: 1,
                    wallet: '+79123221122',
                },
            ],
        };
    },

    computed: {
        paySystemsForm() {
            const data = [];

            for (const id in this.paySystems) {
                data.push({
                    text: this.paySystems[id].name,
                    value: id,
                });
            }

            return data;
        },

        walletsAsOptions() {
            const wallets = [];

            this.wallets.forEach((wallet) => {
                let walletFormatted = wallet.wallet;

                if (wallet.pay_system == 1) {
                    walletFormatted = this.$format.phone(walletFormatted);
                } else if (wallet.pay_system == 4) {
                    walletFormatted = this.$format.card(walletFormatted);
                }

                wallets.push({
                    text: this.getPaySystemData(wallet.pay_system, wallet.wallet).name,
                    description: walletFormatted,
                    value: wallet.id,
                });
            });

            if (this.wallets.length < this.maxWalletsCount) {
                wallets.push({
                    text: 'Добавить способ вывода',
                    value: 0,
                });
            }

            return wallets;
        },

        STATUSES() {
            return {
                0: {
                    type: 'warning',
                    text: 'Ожидает',
                },
                1: {
                    type: 'success',
                    text: 'Проведен',
                },
                2: {
                    type: 'danger',
                    text: 'Отменен',
                },
            };
        },
    },

    watch: {
        modalWithdraw() {
            this.inputAmount = null;
        },

        modalEditWallets() {
            this.editWalletsAddSelectedType = null;
        },
    },

    mounted() {
        this.loadHistory();
    },

    methods: {
        onWithdraw() {
            alert();
        },

        onSelectEditWallets(id) {
            if (id != 0) return;

            this.$nextTick(() => {
                this.modalEditWallets = true;
                this.$refs.fieldSelectWallet.setValue();
            });
        },

        getPaySystemData(id, wallet) {
            return id == 4 ? BankCard.getType(wallet) : this.paySystems[id];
        },

        loadHistory() {
            this.statusLoadingHistory = true;

            const randomInteger = (min, max) => {
                // получить случайное число от (min-0.5) до (max+0.5)
                const rand = min - 0.5 + Math.random() * (max - min + 1);
                return Math.round(rand);
            };

            setTimeout(() => {
                for (let i = 0; i < 10; i++) {
                    const ps = randomInteger(1, 4);

                    const withdraw = {
                        pay_system: ps,
                        amount: randomInteger(1, 10000),
                        date: Date.now() - (1000 * randomInteger(1, 60 * 60 * 24 * 14)),
                        status: this.STATUSES[randomInteger(0, 2)],
                        wallet: 12341231233,
                    };

                    switch (ps) {
                    case 1:
                        withdraw.wallet = '+79123456789';
                        break;
                    case 4:
                        withdraw.wallet = '4211222233334444';
                        break;
                    }

                    this.withdrawHistory.push(withdraw);
                }

                this.statusLoadingHistory = false;
            }, 0);
        },

        addWallet(formData, finishLoading) {
            // тут какой-то запрос на бекенд

            this.modalEditWallets = false;

            this.$nextTick(() => {
                const wallet_id = 123;

                this.wallets.push({
                    id: wallet_id,
                    pay_system: formData.get('pay_system'),
                    wallet: formData.get('wallet'),
                });

                this.$refs.fieldSelectWallet.setValue(wallet_id);
            });
        },

        deleteWallet(id) {
            const wallet = this.wallets.find((wallet) => wallet.id == id);
            const { name } = this.getPaySystemData(wallet.pay_system, wallet.wallet);

            this.$message.process(`Удаление способа вывода ${name}.`, () => {
                // тут какой-то запрос на бекенд

                const index = this.wallets.findIndex((wallet) => wallet.id == id);
                this.wallets.splice(index, 1);
            });
        },
    },
};
</script>

